@mixin regularstyles {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  speak: none;
  text-decoration: none;
  text-transform: none;
  font-family: "mdspcssregular" !important;
}

.iconMdsp {
  @include regularstyles;
}

@mixin iconMdsp-regular($filename, $insert: before) {
  &:#{$insert} {
    @include regularstyles;
    content: regularchar($filename);
  }
}

