.badge {
    border-radius: 15px;
    box-sizing: border-box;
    display: inline-block;
    font-family: $defaultFont;
    // we do not set rem here on purpose since the badges shall not scale with increasing font size (scaling problems with icon positioning which are bigger then font size of badges)
    font-size: 11px;
    height: 20px;
    font-weight: bold;
    line-height: 16px;
    margin-left: 4px;
    margin-right: 4px;
    padding: 2px 6px 2px 6px;
    position: relative;
    vertical-align: baseline;
    white-space: nowrap;

    &.badge--button {
        height: 24px;
        padding: 4px 10px 4px 8px; // padding-right of 10px to compansate the negative margin-right of the badge

        &[class^='has-borderColor-'],
        &[class*=' has-borderColor-'] {
            padding: 3px 9px 3px 7px; // same padding ad above but minus the 1px border
        }

    }

    &[class^='has-borderColor-'],
    &[class*=' has-borderColor-'] {
        padding: 1px 5px;
    }
}

.badge--dark {
    background-color: $gray400;
    color: $white;
}

.badge--light {
    background-color: $gray200;
    color: $gray800;
}

.button {
    .badge {
        font-size: 11px;
        margin-bottom: -4px;
        margin-top: -2px;
        padding: 2px 6px;
        top: -1px;
        vertical-align: baseline;

        &[class^='has-borderColor-'],
        &[class*=' has-borderColor-'] {
            padding: 1px 5px;
        }
    }
}