$appbarWidth: 80px;
$appbarWithMobile: 40px;
$mobileToggleHeight: 40px;
$leadingRegionWidth: 320px;
$contextRegionWidth: 320px;
$breakpointLayoutMobile: 1024px;
$breakpointLayoutPushed: 768px;

.appWrapper {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    .appWrapper__mobileToggle {
        display: none;

        .button {
            border-color: desaturate($stateBlue100, 100%);
            margin-top: 0;
            margin-bottom: 0;
            padding: 8px;



            .iconMdsp {
                color: desaturate($stateBlue100, 100%);
            }

            &.is-activated {
                background-color: desaturate($stateBlue050, 100%);
                border-color: desaturate($stateBlue100, 100%);
            }

            &.leadingRegionToggle {
                display: none;
            }

            &.contextRegionToggle {
                display: none;

                .iconMdsp {
                    transform: rotate(180deg);
                }
            }
        }

        @include breakpoint(max-width $breakpointLayoutMobile) {
            background-color: $gray100;
            box-sizing: border-box;
            border-bottom: 1px solid $gray250;
            display: flex;
            flex-direction: row;
            height: 40px;
            left: 0;
            padding: 3px;
            position: absolute;
            right: 0;
            top: 0;
            z-index: 4;

            // filler
            &:before {
                content: '';
                flex-grow: 1;
            }
        }
    }

    .appWrapper__appBar {
        background-color: $gray100;
        border-right: 1px solid $gray250;
        box-sizing: border-box;
        bottom: 0;
        display: flex;
        flex-direction: column;
        list-style-type: none;
        margin: 0;
        overflow-y: auto; // enable scrolling if more items than visible space
        padding-left: 0;
        position: absolute;
        top: $mobileToggleHeight;

        @include breakpoint(min-width $breakpointLayoutMobile+1) {
            top: 0;
        }

        width: $appbarWidth;
        z-index: 4;

        // more styles related to app bar only see appBar.scss

        @include breakpoint(max-width $breakpointLayoutMobile) {
            width: $appbarWithMobile;
            .appBar__item {
                > a {
                    min-height: auto;
                    padding: 8px 5px;
                }
                .iconMdsp {
                    margin: auto;
                }
                .item__title {
                    display: none;
                }
            }

            a.appBar__item {
                min-height: auto;
                padding: 7px 5px 6px 5px;
            }
        }
    }


    .appWrapper__regions {
        box-sizing: border-box;
        bottom: 0;
        position: absolute;
        overflow: hidden;
        right: 0;

        left: 0;

        &.has-appBar {
            left: $appbarWidth;

            @include breakpoint(max-width $breakpointLayoutMobile) {
                left: $appbarWithMobile;
            }
        }

        &:not(.has-appBar) {
            left: 0;

            // if there is appbar Markup but no has-appBar class set on regions wapper,
            // hide it
            + .appWrapper__appBar {
                display: none;
            }
        }

        &:not(.has-leadingRegion) {
            .leadingRegionToggleArea { display: none; }
        }
        &:not(.has-contextRegion) {
            .contextRegionToggleArea { display: none; }
        }

        top: $mobileToggleHeight;

        @include breakpoint(min-width $breakpointLayoutMobile+1) {
            top: 0;
        }
    }

    .leadingRegion,
    .mainRegion,
    .contextRegion {
        bottom: 0;
        box-sizing: border-box;
        overflow-y: auto;
        position: absolute;
        top: 0;

        // 'enable' momentum scrolling for IOS
        @supports (-webkit-overflow-scrolling: touch) {
            overflow-y: scroll;
            -webkit-overflow-scrolling: touch;
        }
    }

    .leadingRegion {
        display: none;

        background-color: $gray100;
        border-right: 1px solid $gray250;
        box-shadow: rgba(0,0,0,0.3) 0 0 8px 0;
        left: 0;
        opacity: 1;
        width: $leadingRegionWidth;
        z-index: 3;
        
        &--hasSpacing {
            padding: 16px 40px 48px 40px;

            @include breakpoint(max-width $breakpointLayoutMobile) {
                padding: 16px 16px 32px 16px;
            }
        }
    }

    .mainRegion {
        background-color: #FFF;
        bottom: 0;
        left: 0;
        right: 0;

        overflow-y: auto;
        padding: 24px 40px 48px 40px;

        // IE and Firefox handle bottom spacing differently, should be adapted / harmonized some time:
        // make sure to check in simplegrid (negative bottom margin in last row)
        //> *:last-child {
        //    //padding-bottom: 48px;
        //}

        @include breakpoint(max-width $breakpointLayoutMobile) {
            padding: 16px 16px 32px 16px;

            // IE and Firefox handle bottom spacing differently, should be adapted / harmonized some time:
            //> *:last-child {
            //    //padding-bottom: 32px;
            //}
        }
    }

    .contextRegion {
        background-color: $gray100;
        box-shadow: rgba(0,0,0,0.3) 0 0 8px 0;
        border-left: 1px solid $gray250;
        display: none;

        right: 0;
        width: $contextRegionWidth;
        z-index: 2;

        &--hasSpacing {
            padding: 16px 40px 48px 40px;

            @include breakpoint(max-width $breakpointLayoutMobile) {
                padding: 16px 16px 32px 16px;
            }
        }
    }

    .leadingRegionToggleArea,
    .contextRegionToggleArea {
        background-color: $white;
        border: 1px solid $gray250;
        bottom: 0;
        display: block;
        height: 48px;
        margin: auto;
        opacity: 0.7;
        position: absolute;
        top: 0;
        transition: opacity 0.2s ease-in-out;
        width: 24px;
        z-index: 10;

        a {
            display: block;
            font-size: 24px;
            text-decoration: none;
            height: 100%;
            line-height: 48px;
            text-align: center;
            transition: transform 0.2s ease-in-out;
            width: 100%;
        }

        a, a:visited {
            color: $gray800;
        }
    }



    .leadingRegionToggleArea {
        border-bottom-right-radius: 3px;
        border-top-right-radius: 3px;
        left: -1px;

        a {
            @include iconMdsp-black(arrowRightExtraSmall);
        }

        &:hover {
            opacity: 1;
        }
    }

    .contextRegionToggleArea {
        border-bottom-left-radius: 3px;
        border-top-left-radius: 3px;
        right: -1px;

        a {
            @include iconMdsp-black(arrowLeftExtraSmall);
        }

        &:hover {
            opacity: 1;
        }
    }

    // only show toggle buttons and region altogether if region is present
    // toggling behaviour {
    .appWrapper__regions {
        &.has-leadingRegion {
            &.leadingRegion-is-expanded {
                .leadingRegion {
                    display: block;
                }

                .leadingRegionToggleArea {
                    left: $leadingRegionWidth - 1;

                    a {
                        transform: rotate(180deg);
                    }
                }
            }
            .leadingRegionToggleArea {
                @include breakpoint (max-width $breakpointLayoutMobile) {
                    display: none;
                }
            }

            & ~ .appWrapper__mobileToggle .leadingRegionToggle {
                display: block;
            }
        }

        &.has-contextRegion {
            &.has-contextRegion.contextRegion-is-expanded {
                .contextRegion {
                    display: block;

                }

                .contextRegionToggleArea {
                    right: $leadingRegionWidth - 1;

                    a {
                        transform: rotate(180deg);
                    }
                }
            }
            .contextRegionToggleArea {
                @include breakpoint (max-width $breakpointLayoutMobile) {
                    display: none;
                }
            }

            & ~ .appWrapper__mobileToggle .contextRegionToggle {
                display: block;
            }
        }

        // if no leading nor context region is present at all and mobile device width
        &:not(.has-contextRegion):not(.has-leadingRegion) {
            background-color: pink;
            top: 0;

            & ~ .appWrapper__mobileToggle {
                display: none;
            }

            & ~ .appWrapper__appBar {
                top: 0;
            }
        }

        // pushing behaviour, only enabled for > 768px
        @include breakpoint(min-width $breakpointLayoutPushed + 1) {
            &.appWrapper__regions--pushLayout {

                &.has-leadingRegion.leadingRegion-is-expanded {
                    .leadingRegion {
                        box-shadow: none;
                    }

                    .mainRegion {
                        left: $leadingRegionWidth;
                    }
                }

                .mainRegion {
                    left: 0;
                    right: 0;
                }

                &.has-contextRegion.contextRegion-is-expanded {
                    .contextRegion {
                        box-shadow: none;
                    }

                    .mainRegion {
                        right: $contextRegionWidth;
                    }
                }
            }
        }
    }
}
