.menu {
  display: inline-block;
  position: relative;
  z-index: 1;

  > .button,
  > button {
    background-color: $white;
    box-sizing: border-box;
    font-size: 24px;
    height: 32px;
    width: 32px;
    @include iconMdsp-black(more);
  }

  .menu__items {
    background-color: $white;
    border: 1px solid $gray250;
    border-radius: 3px;
    box-shadow: 0 4px 10px 0 rgba(0,0,0, 0.10);
    box-sizing: border-box;
    display: none;
    margin-top: -6px;
    max-height: 450px;
    max-width: 320px;
    overflow-y: auto;
    padding: 8px 0px;
    position: absolute;


    .menu__item {
      box-sizing: border-box;
      color: $gray800;
      display: block;
      font-size: 14px;
      font-size: rem(14px);
      height: 36px;
      line-height: 36px;
      padding: 0 30px 0 20px;
      max-height: 36px;
      max-width: 320px;
      overflow: hidden;
      text-decoration: none;
      text-overflow: ellipsis;
      white-space: nowrap;

      .iconMdsp {
        margin: 0 32px 0 0;
        vertical-align: middle;


        &:before {
          font-size: 24px;
          position: absolute;
        }
        position: relative;
      }

      &:hover {
        background-color: $gray100;
      }

      &:focus {
        box-shadow: inset 0 0 0 2px $coolBlue;
        // IE11 does not support outline-offset, hence the feature query
        outline: 0;
        @supports (outline-offset: -2px) {
          box-shadow: none;
          outline-offset: -2px;
          outline: 2px solid $coolBlue;
        }
      }

      &:active,
      &.is-pressed {
        background-color: $gray200;
      }

      &.is-disabled {
        color: rgba($gray800, 0.5);
        pointer-events: none;
        &:hover {
          cursor: not-allowed;
        }
      }
    }
  }

  &.is-expanded {
    > .button,
    > button {
      background-color: $stateBlue050;
      border: 1px solid $stateBlue100;
    }
    .menu__items {
      display: block;
    }
  }

  &.is-right-aligned {
    text-align: right;

    .menu__items {
      text-align: left;
      right: 0;
    }
  }

  &.is-bottom-aligned {
    .menu__items {
      bottom: 0;
      margin-bottom: 42px;
    }
  }
}

// if menu is preceeded by a button, add a spacing on the left
button,
.button {
    + .menu {
        margin-left: 8px;
        // if menu is preceeded by a button, add a spacing on the left
    }
}
