.appWrapper__appBar {

    // in case the pattern is used without <li> elements but with <a> elements instead
    // https://code.siemens.com/mds/mds/issues/397
    a.appBar__item {
        box-sizing: border-box;
        min-height: 74px;
        padding: 6px 5px 12px 5px;
        position: relative;
        text-decoration: none;

        .iconMdsp {
            margin: 7px auto 7px auto;
        }

        &:hover {
            background-color: $white;

            &,
            .iconMdsp,
            .item__title {
                color: $black;
            }
        }

        &:focus,
        &.is-focused {
            background-color: $white;
            box-shadow: inset 0 0 0 2px $coolBlue; // border would be cut off on left and right;
            outline: 0;

            &,
            .iconMdsp,
            .item__title {
                color: $black;
            }
        }

        &:active {
            background-color: $gray200;
            box-shadow: none;
            outline: 0;
        }

        &.is-disabled {
            background-color: $gray100;
            pointer-events: none;

            &:focus {
                box-shadow: none;
                outline: 0;
            }

            &:hover {
                cursor: not-allowed;
            }

            .iconMdsp,
            .item__title {
                color: rgba($gray650, 0.4);
            }
        }
    }

    // 'default' markup with <ul> and <li> elements
    .appBar__item {
        display: block;
        flex-basis: auto; // IE 11 does not like 'content' nor fixed pixel values
        flex-grow: 0;
        flex-shrink: 0;
        justify-content: flex-start;
        margin: 0 !important;
        position: relative;
        text-align: center;

        & + .appBar__item {
            margin-top: 1px !important;
        }

        &:first-child {
            margin-top: 8px !important;
        }

        .iconMdsp {
            color: $gray650;
            display: block;
            font-size: 24px;
            margin: 7px auto 8px auto;
        }

        .item__title {
            color: $gray650;
            display: block;
            font-size: 12px;
            line-height: 14px;
        }

        > a {
            box-sizing: border-box;
            color: $gray650;
            display: block;
            min-height: 74px;
            padding: 8px 5px 13px 5px;

            transition: background-color 0.2s ease-in-out;

            .iconMdsp,
            .item__title {
                transition: color 0.2s ease-in-out;
            }

            &, &:visited, &:active, &:focus {
                text-decoration: none;
            }
        }
        > a:hover {
            text-decoration: none;
        }

        &.is-activated {
            background-color: $white;

            &:before {
                background-color: $stateBlue100;
                bottom: 0;
                content: '';
                display: block;
                left: 0;
                position: absolute;
                top: 0;
                width: 3px;
            }

            > a,
            .iconMdsp,
            .item__title {
                color: $stateBlue100;
            }
        }

        & > a:hover {
            background-color: $white;

            &,
            .iconMdsp,
            .item__title {
                color: $black;
            }
        }

        & > a:focus,
        &.is-focused > a {
            background-color: $white;
            box-shadow: inset 0 0 0 2px $coolBlue; // border would be cut off on left and right;
            outline: 0;

            &,
            .iconMdsp,
            .item__title {
                color: $black;
            }
        }

        & > a:active {
            background-color: $gray200;
            box-shadow: none;
            outline: 0;
        }

        &.is-disabled > a {
            background-color: $gray100;
            pointer-events: none;

            &:focus {
                box-shadow: none;
                outline: 0;
            }

            &:hover {
                cursor: not-allowed;
            }

            .iconMdsp,
            .item__title {
                color: rgba($gray650, 0.4);
            }
        }
    }
}
