@mixin blackstyles {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  speak: none;
  text-decoration: none;
  text-transform: none;
  font-family: "mdspcssblack" !important;
}

.iconMdsp.black {
  @include blackstyles;
}

@mixin iconMdsp-black($filename, $insert: before) {
  &:#{$insert} {
    @include blackstyles;
    content: blackchar($filename);
  }
}

