.notificationContainer {
    bottom: 0;
    position: fixed;
    right: -320px;
    top: 0;
    width: 320px;

    // make sure notifications are ABOVE busy indicator which is ABOVE dialogs / confirms
    z-index: 10000;
}

.notifications {
    font-family: $defaultFont;
    list-style-type: none;
    margin: 52px 0 0 0 !important;
    margin-block-start: 0;
    padding: 0 !important;
    padding-inline-start: 0;

    .notification {
        background-color: $contextBlue050;
        border: 1px solid $contextBlue150;
        border-radius: 3px;
        box-sizing: border-box;
        color: $contextBlue600;
        margin: 0 0 8px -336px;
        position: relative;
        width: 320px;

        &.is-animated {
            max-height: 400px; // attention: if notifications filled more then 400px, overflow occurs
            transition: margin-left 0.4s ease-in-out,
                max-height 0.2s 0.45s linear,
                margin-bottom 0.2s 0.45s linear,
                border-width 0s 0.45s linear;
        }

        &.is-hidden {
            border-width: 0 !important;
            margin-left: 0;
            margin-bottom: 0;
            max-height: 0;
            overflow: hidden;
        }

        // shadow (box) layer
        &:after {
            bottom: 0;
            box-shadow: 0 2px 20px 0 rgba(0,0,0, 0.15);
            display: block;
            content: '';
            left: 16px;
            position: absolute;
            right: 16px;
            top: 16px;
            z-index: -1;
        }

        // main content of notification
        &__main {
            margin: 12px 16px 12px 56px;
            position: relative;

            @include iconMdsp-black(information);

            &:before {
                font-size: 24px;
                font-size: rem(24px);
                font-style: normal;
                font-variant: normal;
                font-weight: normal;
                left: -40px;
                line-height: 24px;
                position: absolute;
                top: calc(50% - 12px);
            }

            p:last-child {
                margin-bottom: 0;
            }

            .notification__title {
                font-weight: bold;
                font-size: 14px;
                font-size: rem(14px);
                line-height: 20px;
                line-height: rem(20px);
                margin: 0;
                padding-right: 8px;
            }

            .notification__content {
                font-size: 14px;
                font-size: rem(14px);
                line-height: 20px;
                line-height: rem(20px);
                // max-height: 8em;
                // overflow: auto;

                word-break: break-word;
                word-wrap: break-word;
                overflow-wrap: break-word;
            }
        }

        // optional 'more details' content
        &__details {
            border-top: 1px solid rgba($gray250, 0.5);
            font-size: 12px;
            font-size: rem(12px);
            line-height: 16px;
            line-height: rem(16px);
            margin: 0;
            padding: 0;
            position: relative;

            .details__expander {
                color: $contextBlue600;
                display: block;
                padding: 8px 16px 8px 36px;
                margin-left: 0;
                position: relative;
                text-decoration: none;
                @include iconMdsp-black(arrowRightExtraSmall);

                &:hover {
                    color: $coolBlue !important;
                }

                &:before {
                    font-size: 24px;
                    left: 12px;
                    position: absolute;
                }
            }

            .details__content {
                display: none;
                padding: 0 16px 8px 16px;

                word-break: break-word;
                word-wrap: break-word;
                overflow-wrap: break-word;

                li {
                    margin: 0 0 0.2em 0;
                }

                table,
                .table {
                    margin: 0;

                    th, td {
                        font-size: 12px;
                        font-size: rem(12px);
                    }
                }

                .keyValue__key,
                .keyValue__value {
                    font-size: 12px;
                    font-size: rem(12px);
                }
            }

            &.is-expanded {
                .details__expander {
                    @include iconMdsp-black(arrowDownExtraSmall);
                }
                .details__content {
                    display: block;
                }
            }
        }

        .notification__close {
            box-sizing: border-box;
            display: inline-block;
            font-size: 24px;
            height: 24px;
            position: absolute;
            right: -12px;
            top: -8px;
            width: 24px;

            @include iconMdsp-regular(cancel);
            text-decoration: none;
            transition: color 0.2s ease-in-out;

            &, &:visited {
                color: $contextBlue600;
            }

            &:hover {
                color: $coolBlue !important;
                text-decoration: none;
            }
        }
    }

    .notification.is-info {
        background-color: $contextBlue050;
        border: 1px solid $contextBlue150;
        color: $contextBlue600;

        .notification__main {
            @include iconMdsp-black(information);
        }

        .notification__close,
        .details__expander {
            &,
            &:visited {
                color: $contextYellow600;
            }
        }
    }

    .notification.is-warning {
        background-color: $contextYellow;
        border: 1px solid $contextYellow100;
        color: $contextYellow600;

        .notification__main {
            @include iconMdsp-black(exclamationDiamond);
        }

        .notification__close,
        .details__expander {
            &,
            &:visited {
                color: $contextYellow600;
            }
        }
    }

    .notification.is-error,
    .notification.is-alert {
        background-color: $contextRed;
        border: 1px solid $contextRed200;
        color: $contextRed500;

        .notification__main {
            @include iconMdsp-black(errorAlert);
        }

        .notification__close,
        .details__expander {
            &,
            &:visited {
                color: $contextRed500;
            }
        }
    }

    .notification.is-success {
        background-color: $contextGreen050;
        border: 1px solid $contextGreen200;
        color: $contextGreen600;

        .notification__main {
            @include iconMdsp-black(ok);
        }

        .notification__close,
        .details__expander {
            &,
            &:visited {
                color: $contextGreen600;
            }
        }
    }
}

// responsive variant for small devices
@include breakpoint($xs) {
    .notificationContainer {
        width: 100vw;
        right: -100vw;
    }

    .notifications {
        .notification {
            width: calc(100% - 16px);
            margin: 0 8px 8px calc(-100% + 8px);
        }
    }
}
