@mixin boldstyles {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  speak: none;
  text-decoration: none;
  text-transform: none;
  font-family: "mdspcssbold" !important;
}

.iconMdsp.bold {
  @include boldstyles;
}

@mixin iconMdsp-bold($filename, $insert: before) {
  &:#{$insert} {
    @include boldstyles;
    content: boldchar($filename);
  }
}

@function boldchar($filename) {
  $char: "";

  @if $filename == analysis {
    $char: "\E001";
  }
  @if $filename == backButtonNarrow {
    $char: "\E002";
  }
  @if $filename == buildingBlock {
    $char: "\E003";
  }
  @if $filename == cancel {
    $char: "\E004";
  }
  @if $filename == certificate {
    $char: "\E005";
  }
  @if $filename == comment {
    $char: "\E006";
  }
  @if $filename == devices {
    $char: "\E007";
  }
  @if $filename == download {
    $char: "\E008";
  }
  @if $filename == edit {
    $char: "\E009";
  }
  @if $filename == export {
    $char: "\E00A";
  }
  @if $filename == fileTree {
    $char: "\E00B";
  }
  @if $filename == filter {
    $char: "\E00C";
  }
  @if $filename == filterAlternative {
    $char: "\E00D";
  }
  @if $filename == fullscreen {
    $char: "\E00E";
  }
  @if $filename == goTo {
    $char: "\E00F";
  }
  @if $filename == import {
    $char: "\E010";
  }
  @if $filename == navigateLeftSmall {
    $char: "\E011";
  }
  @if $filename == navigateRightSmall {
    $char: "\E012";
  }
  @if $filename == notificationSquare {
    $char: "\E013";
  }
  @if $filename == outgoingSmall {
    $char: "\E014";
  }
  @if $filename == person {
    $char: "\E015";
  }
  @if $filename == search {
    $char: "\E016";
  }
  @if $filename == sort {
    $char: "\E017";
  }
  @if $filename == update {
    $char: "\E018";
  }
  @if $filename == updateCog {
    $char: "\E019";
  }
  @if $filename == upload {
    $char: "\E01A";
  }
  @return $char;
}

