// simple card 'grid' which uses flexbox to be backwards compatible, especially for IE
.cards.cards--simpleGrid {

    // negative margin used on wrapper since we dont have to adapt n-th child margin right lateron
    // (it is even not possible since cards with huge content might break / wrap on other card cols already
    margin-left: -8px;
    margin-right: -8px;
    margin-bottom: -16px;

    // IE and Firefox handle bottom spacing differently, should be adapted / harmonized some time:
    //padding-bottom: 32px !important;
    //@include breakpoint(max-width 1024px) {
    //    padding-bottom: 16px !important;
    //}

    .cards__row {
        display: flex;
        flex-grow: 0;
        flex-shrink: 1;
        flex-basis: auto;
        flex-direction: row;
        flex-wrap: wrap;

        // needed for shadows to work when contained in parent elements with position relative, otherwise shadow will be put behind that parent
        position: relative;
        z-index: 1;

        .cards__col {
            flex-grow: 1;
            flex-shrink: 0;
            flex-basis: 0;

            ///* two cards */
            //&:first-child:nth-last-child(2),
            //&:first-child:nth-last-child(2) ~ .cards__col {
            //    flex-basis: 50%
            //}

            /* three cards */
            @include breakpoint($sm) { // SM
                &:first-child:nth-last-child(3),
                &:first-child:nth-last-child(3) ~ .cards__col {
                    // -16px has to be removed -> 8 + 8 margin of columns
                    flex-basis: calc(100% - 16px);
                }
            }

            /* four cards */
            @include breakpoint($sm, $md) { // SM, MD
                &:first-child:nth-last-child(4),
                &:first-child:nth-last-child(4) ~ .cards__col {
                    flex-basis: 48%;

                    //&:nth-child(2) {
                    //    margin-right: 0;
                    //}
                }
            }

            /* five cards */
            @include breakpoint($sm, $md) { // SM, MD
                &:first-child:nth-last-child(5),
                &:first-child:nth-last-child(5) ~ .cards__col {

                    &:nth-child(1),
                    &:nth-child(2) {
                        flex-basis: 48%;
                    }

                    //&:nth-child(2) {
                    //    margin-right: 0;
                    //}

                    &:nth-child(3),
                    &:nth-child(4),
                    &:nth-child(5) {
                        flex-basis: 31%;
                    }
                }
            }

            /* six cards */
            //@include breakpoint(min-width 1300px) { // LG
            //    &:first-child:nth-last-child(6),
            //    &:first-child:nth-last-child(6) ~ .cards__col {
            //        //flex-basis: 13%;
            //
            //    }
            //}
            @include breakpoint($md) { // MD
                &:first-child:nth-last-child(6),
                &:first-child:nth-last-child(6) ~ .cards__col {
                    & {
                        flex-basis: 31%;
                    }

                    //&:nth-child(3) {
                    //    margin-right: 0;
                    //}
                }
            }
            @include breakpoint($sm) { // SM
                &:first-child:nth-last-child(6),
                &:first-child:nth-last-child(6) ~ .cards__col {
                    & {
                        flex-basis: 48%;
                    }

                    //&:nth-child(2),
                    //&:nth-child(4) {
                    //    margin-right: 0;
                    //}
                }
            }
            // general width for every column below smallest breakpoint
            @include breakpoint($xs) {
                // -16px has to be removed -> 8 + 8 margin of columns
                flex-basis: calc(100% - 16px);
            }


            // instead of setting a height of 100% to the childs (= .card)
            display: flex;

            align-items: stretch;
            flex-direction: column;
            margin-left: 8px;
            margin-right: 8px;
            margin-bottom: 16px;
            max-width: 100%;

            //&:first-child {
            //    margin-left: 0;
            //}
            //
            //&:last-child {
            //    margin-right: 0;
            //}
        }


    }
}

.card {
    background-color: $white;
    border: 1px solid $gray250;
    border-radius: 3px;
    box-sizing: border-box;

    display: flex;
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: auto;
    flex-direction: column;
    position: relative;

    // make sure (just in case) other links in the card are clickable
    & {
        > a,
        > p a,
        > div a,
        > div button,
        > p button {
            position: relative;
            &:after {
                display: none;
            }
        }
    }

    // remove positioning from header wrapper to make title link wrap complete title,
    // overwrites relative positioning from above lines
    & > .card__header .card__title > a {
        position: static;
    }

    .card__header {
        padding: 16px 24px;
        .card__title {
            font-size: 17px;
            font-size: rem(17px);
            line-height: 23px;
            line-height: rem(23px);
            font-weight: bold;
            margin-bottom: 0;
            margin-right: 20px;
            margin-top: 0px;

            a {
                color: $gray800;
                text-decoration: none;

                &:before {
                    bottom: 0;
                    content: "";
                    display: block;
                    left: 0;
                    position: absolute;
                    right: 0;
                    top: 0;
                }

                @include iconMdsp-regular(outgoingInApp, after);
                &:after {
                    display: inline-block;
                    position: relative;
                    color: $coolBlue;
                    top: 0px;
                    left: 4px;
                    font-size: 16px;
                    font-size: rem(16px);

                }

                &:focus {
                    outline: none;
                    &:before {
                        outline: 2px solid $coolBlue;
                    }
                }

                &.link--noIcon {
                    &:after {
                        content: '';
                    }
                }
            }
        }

        .card__subline {
            color: $gray400;
            font-size: 14px;
            font-size: rem(14px);
            line-height: 20px;
            line-height: rem(20px);

            margin-bottom: 0;
            margin-top: 0;
        }

        .menu {
            position: absolute;
            top: 4px;
            right: 12px;
        }
    }

    .card__content {
        > p {
            font-size: 14px;
            font-size: rem(14px);
            padding: 0 24px;
        }

        > p:first-child {
            margin-top: 0;
        }
    }

    .card__footer {
        margin-top: auto;
        > button,
        .button {
            margin-bottom: 8px;
            margin-top: 0;
            margin-left: 0;
            margin-right: 8px;
        }
        padding: 24px 24px 16px 24px;
    }

    // shadow 'layer' / element default
    &:after {
        bottom: 0px;
        box-shadow: 0 2px 10px 0 rgba(0,0,0, 0.5);
        display: block;
        content: '';
        left: 8px;
        position: absolute;
        right: 8px;
        top: 16px;
        z-index: -1;
        transition: opacity 0.2s ease-in-out;
    }

    &:hover:after {
        opacity: 0;
    }


    // shadow 'layer' / element for hover
    &:before {
        bottom: 0;
        box-shadow: 0 3px 20px 0 rgba(0,0,0, 0.5);
        display: block;
        content: '';
        left: 6px;
        position: absolute;
        right: 6px;
        top: 12px;
        z-index: -1;
        opacity:0;
        transition: opacity 0.2s ease-in-out;
    }

    // IE and Edge dont know the focus-within selector, therefor we cannot combine it with the following selector
    &:focus-within:before {
        opacity: 1;
    }
    &:hover:before {
        opacity: 1;
    }

    &.card--centered {
        .card__header,
        .card__content,
        .card__footer {
            text-align: center;
        }
    }
}
