.button,
a.button {

    &:link,
    &:visited {
        color: $white;
    }

    border-radius: 3px;
    box-sizing: border-box;
    cursor: pointer;
    display: inline-block;
    font-family: $defaultFont;
    font-size: 14px;
    font-size: rem(14px);
    line-height: 14px;
    line-height: rem(14px);
    margin: 8px 0px;
    margin: rem(8px 0px);
    padding: 8px 24px;
    padding: rem(8px 24px);
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    white-space: nowrap;

    @include hoverTransition($animate: (background-color, color, border));

    & + .button {
        margin-left: 8px;
    }

    &[type="submit"] { // disable 'native' IOS look on iPhones
        -webkit-appearance: none;
    }

    &:focus {
        outline: 2px solid $coolBlue;
        outline-offset: -2px;

    }


    // remove dotted inner border in Firefox browsers
    &::-moz-focus-inner {
        border: 0;
    }

    &.button--primary {
        background-color: $stateBlue100;
        border: 1px solid transparent;
        color: $white !important;
        text-decoration: none;

        &:link, &:visited {
            text-decoration: none;
        }

        &:hover {
            background-color: $coolBlue;
            text-decoration: none;
        }

        &:active {
            background-color: $stateBlue250;
            outline: none;
            text-decoration: none;
        }

        &:disabled,
        &[disabled],
        &.is-disabled {
            background-color: $gray400;
            color: $white;
            &:hover {
                cursor: not-allowed;
            }
        }
    }

    &.button--secondary {
        background-color: transparent;
        border: 1px solid $stateBlue100;
        color: $stateBlue100;
        text-decoration: none;

        &:link, &:visited {
            color: $stateBlue100;
            text-decoration: none;
        }

        &:hover {
            background-color: $stateBlue020;
            text-decoration: none;
        }

        &:active {
            background-color: $stateBlue050;
            outline: none;
            text-decoration: none;
        }

        &:disabled,
        &[disabled],
        &.is-disabled {
            background-color: $white;
            border-color: $gray400;
            color: $gray400;
            cursor: not-allowed;
        }
    }

    &.button--ghost {
        background-color: transparent;
        color: $stateBlue100;
        font-family: $defaultFont;
        border: 1px solid transparent;
        padding: 8px;
        padding: rem(8px);
        text-decoration: none;

        &:link, &:visited {
            color: $stateBlue100;
            text-decoration: none;
        }

        &:hover {
            background-color: $stateBlue020;
            text-decoration: none;
        }

        &:active {
            background-color: $stateBlue050;
            outline: none;
            text-decoration: none;
        }

        &:disabled,
        &[disabled],
        &.is-disabled {
            background-color: $white;
            color: $gray400;
            cursor: not-allowed;
        }
    }

    &.button--secondaryContentAction,
    &.button--primaryContentAction {
        background-color: transparent;
        border: 1px solid transparent;
        color: $stateBlue100;
        font-family: $defaultFont;
        margin: 0 8px;
        padding: 8px 2px 8px 0px;
        padding: rem(8px 2px 8px 0px);
        position: relative;
        text-decoration: none;

        &:link, &:visited {
            color: $stateBlue100;
            text-decoration: none;
        }

        &:hover {
            color: $coolBlue;
            text-decoration: none;
        }

        &:focus {
            outline-offset: 0px;
        }

        &:active {
            color: $stateBlue250;
            outline: none;
        }

        &:disabled,
        &[disabled],
        &.is-disabled {
            background-color: transparent;
            color: $gray400;
            cursor: not-allowed;
        }
    }

    &.button--primaryContentAction {
        font-weight: bold;
        padding: 12px 2px 12px 0px;
        padding: rem(12px 2px 12px 0px);
    }

    &.button--split {
        background-color: $white;
        border-color: $gray250;
        border-radius: 0;
        border-width: 0px;
        box-sizing: border-box;

        color: $gray800;
        display: block;
        margin: 0;
        padding: 0 30px 0 20px;

        font-size: 14px;
        font-size: rem(14px);
        height: 36px;
        line-height: 36px;

        text-align: left;

        &:link, &:visited {
            color: $stateBlue100;
            text-decoration: none;
        }

        &:hover {
            background-color: $gray100;
            text-decoration: none;
        }

        &:active {
            background-color: $gray200;
            outline: none;
            text-decoration: none;
        }

        &:focus {
            box-shadow: inset 0 0 0 2px $coolBlue;
            // IE11 does not support outline-offset, hence the feature query
            outline: 0;
            @supports (outline-offset: -2px) {
                box-shadow: none;
                outline-offset: -2px;
                outline: 2px solid $coolBlue;
            }
        }

        &:disabled,
        &[disabled],
        &.is-disabled {
            background-color: $white;
            border-color: $gray400;
            color: $gray400;
            cursor: not-allowed;
        }

        .iconMdsp {
            vertical-align: middle;
        }
    }

    .iconMdsp {
        display: inline-block;
        font-size: 24px;
        font-size: rem(24px);
        line-height: 14px;
        line-height: rem(14px);

        // the -2px on the left is to balance the center-alignment within the button (most icons are not completely covering 24x24px)
        margin: 0 4px 0 -2px;
        margin: rem(0 4px 0 -2px);
        vertical-align: bottom;
    }

    &.has-icon-right {
        .iconMdsp {
            // the -2px on the right is to balance the center-alignment within the button (most icons are not completely covering 24x24px)
            margin: 0 -2px 0 4px;
            margin: rem(0 -2px 0 4px);
        }
    }

    &.has-icon-only {
        padding: 8px 3px;
        padding: rem(8px 3px);
        .iconMdsp {
            margin: 0;
        }
    }
}

.buttonGroup {
    display: inline-flex;
    flex-wrap: wrap;

    // moved half of the vertical margin (4px) to the wrapper.
    // If the button group breaks, it then has a vertical spacing of 4px, instead of 8px. The outer padding of the wrapper is still 8px
    //margin: 4px 0 4px 0;
    //margin: rem(4px 0 4px 0);
    vertical-align: middle;

    + .buttonGroup {
        margin-left: 12px;
        margin-left: rem(12px);
    }

    .button,
    a.button {
        margin: 0 0 4px 0;
        margin: rem(0 0 4px 0);

        margin-left: -1px;

        &.button--primary,
        &.button--secondary {
            border-color: $gray400;
            border-radius: 0;
            color: $gray800;

            padding-left: 4px;
            padding-left: rem(4px);
            padding-right: 4px;
            padding-right: rem(4px);

            transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;

            &:last-child {
                border-right-width: 1px; // for compensating 1px border
            }

            &:first-child {
                border-top-left-radius: 3px;
                border-bottom-left-radius: 3px;
            }

            &:last-child {
                border-bottom-right-radius: 3px;
                border-top-right-radius: 3px;
            }

            &:hover {
                background-color: $gray250;
            }

            &:active,
            &.is-activated {
                background-color: $gray650;
                border-color: $gray650;
                color: $white;
                outline: none;
                text-decoration: none;
            }

            &:disabled,
            &[disabled],
            &.is-disabled {
                color: $gray250;
                background-color: $gray100;
            }
        }

        &.button--ghost {
            color: $gray800;
            transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;

            &:hover {
                background-color: $gray250;
            }

            &:active,
            &.is-activated {
                background-color: $gray650;
                color: $white;
                outline: none;
                text-decoration: none;
            }

            &:disabled,
            &[disabled],
            &.is-disabled {
                color: $gray250;
                background-color: $gray100;
            }
        }
    }
}

.buttonSplitWrapper {
    align-items: flex-end;
    display: inline-flex;
    flex-direction: column;
    justify-content: flex-end;

    + .buttonSplitWrapper {
        margin-left: 12px;
        margin-left: rem(12px);
    }

    .buttonGroup {
        margin-bottom: 0;

        .button--secondary {
            margin-bottom: 2px;

            padding-left: 16px;
            padding-left: rem(16px);
            padding-right: 16px;
            padding-right: rem(16px);

        }

        .button--secondary.has-icon-only {
            padding: 8px 3px;
        }
    }

    .buttonSplit {
        display: none;

        box-shadow: 0 4px 10px 0 rgba(0,0,0, 0.10);
        border: 1px solid $gray250;
        border-radius: 3px;
        flex-direction: column;
        padding: 8px 0px;
        position: relative;
        overflow-y: auto;

        &.is-activated {
            display: inline-flex;
        }
    }
}

//removeIf(sassHandover)
// style native elements when forced defaults
&.#{$forceDefaultClass} {
    button,
    button[type=button],
    input[type=button],
    input[type=reset],
    input[type=submit] {
        @extend .button;
    }
    button + .button,
    button + button,
    .button + button {
        margin-left: 8px;
    }
}
//endRemoveIf(sassHandover)
